@use '../../../styles/block/utils/variables.scss' as *;

.theme__area {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.theme__switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 14px;
  padding-bottom: 2px;
  background-color: $text-color;
  border-radius: 50px;
  border: none;
  box-shadow: 0 0 18px $text-color;
  cursor: pointer;
}

.theme__switch::after {
  content: '';
  position: absolute;
  display: block;
  background-color: $background-color;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  top: 1px;
  left: 1px;
}

.theme__checkbox:checked + .theme__switch::after {
  transform: translateX(137%);
  transition: all 0.4s cubic-bezier(0.17, 0.88, 0.32, 1);
}

.theme__checkbox + .theme__switch::after {
  transition: all 0.4s cubic-bezier(0.17, 0.88, 0.32, 1);
}

.theme__checkbox {
  display: none;
}
